/* global jQuery TimelineLite Waypoint MktoForms2*/
/*
 * Slide form down on mobile when button is clicked
 */
(function ($) {

	"use strict";

	var form__wrapper = document.querySelector(".form__wrapper") || null;
	var ease = window.Power4.easeInOut;

	function showForm() {
		var tShow = new TimelineLite();
		tShow.addLabel("start");
		tShow.fromTo(form__wrapper, 1, { y: "-100%", opacity: 0 }, { y: "0%", opacity: 1, ease: ease }, "start+=0");
	}

	function hideForm() {
		$(".form__wrapper").removeClass("overlay");
		var tHide = new TimelineLite();
		tHide.addLabel("start");
		tHide.fromTo(form__wrapper, 1, { y: "0%", opacity: 1 }, { y: "-100%", opacity: 0, ease: ease},  "start+=0");
	}

	if (form__wrapper !== null) {
		$(".heroContainer .q-button, .try-it .q-button").on("click", function () {
			$(".form__wrapper").addClass("overlay");
			showForm();
		});

		$(".close-overlay").on("click", function () {
			hideForm();
		});
	}

}(jQuery));

/*
 * animate heading and computers
 */
(function () {

	"use strict";

	var headingAnimated = false;
	var ease = window.Power4.easeInOut;

	new Waypoint({
		element: document.getElementById("qitam"),
		offset: "80%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !headingAnimated) {
				t1.fromTo(".intro-container .heading--2", 1, { y: "-10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: window.Power4.easeInOut });
				t1.fromTo(".intro-container .paragraph--1", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: window.Power4.easeInOut });
			}

			headingAnimated = true;
		}
	});

	var computersDesktopAnimated = false;

	new Waypoint({
		element: document.getElementById("computers"),
		offset: "80%",
		handler: function(direction) {
			var playButton = document.getElementById("play-button");
			var pauseButton = document.getElementById("pause-button");
			var t0 = new TimelineLite();
			var t1 = new TimelineLite({onComplete:function() {
				this.restart();
			}
			});

			if (direction === "down" && !computersDesktopAnimated) {
				// screenshot 1
				t1.to(".screen1", 1, { left: "-100%", ease: ease }, "2"); // 2s later, slide left screen 1
				t1.to(".screen1", 0, { left: "100%", ease: ease }, "+=1"); // after 1s, reset the left position of screen 1
				// screenshot 2
				t1.to(".screen2", 1, { left: "0", ease: ease }, "-=2"); // slide left with previous animation
				t1.to(".screen2", 1, { top: "-23%", ease: ease }, "+=1.2"); // 1.2s later, slide up
				t1.to(".screen2", 1, { left: "-100%", ease: ease }, "+=1"); // 1s later, slide left
				// screenshot 3
				t1.to(".screen3", 1, { left: "10px", ease: ease }, "-=1"); // slide left with previous animation
				t1.to(".screen3", 1, { top: "-11%", ease: ease }, "+=1.2"); // 1.2s later, slide up
				t1.to(".screen3", 1, { left: "-100%", ease: ease }, "+=1"); // 1s later, slide left
				// screenshot 4
				t1.to(".screen4", 1, { left: "0", ease: ease }, "-=1"); // slide left with previous animation
				t1.to(".screen4", 1, { top: "-71%", ease: ease }, "+=1.2"); // 1.2s later, slide up
				t1.to(".screen4", 1, { left: "-100%", ease: ease }, "+=1.5"); // 1.5s later, slide left
				// screenshot 5
				t1.to(".screen5", 1, { left: "0", ease: ease }, "-=1"); // slide left with previous animation
				t1.to(".screen5", 1, { top: "-22%", ease: ease }, "+=1.2"); // 1.2s later, slide up
				t1.to(".screen5", 1, { left: "-100%", ease: ease }, "+=1"); // 1s later, slide left
				t1.to(".screen1", 1, { left: "0", ease: ease }, "-=1"); // slide left screen 1
			}

			computersDesktopAnimated = true;

			// play/pause the animation on click
			var screenshots = document.getElementById("computers");

			screenshots.addEventListener("click", function () {
				var isPaused = t1.paused();
				if (isPaused) {
					playButton.style.opacity = "0.65";
					t0.to(playButton, 1, { autoAlpha: 0, scale: 1.6, ease: ease});
					t0.to(playButton, 1, { scale: 1});
					t1.play();
				} else {
					pauseButton.style.opacity = "0.65";
					t0.to(pauseButton, 1, { autoAlpha: 0, scale: 1.6, ease: ease});
					t0.to(pauseButton, 1, { scale: 1});
					t1.pause();
				}
			});
		}
	});

}());


/*
 * populate "position" field
 */
(function ($,MktoForms2) {

	"use strict";

	MktoForms2.whenReady(function (form) {
		form.getFormElem().find("[name=formPosition]").val("top");
	});

}(jQuery,MktoForms2));
